export default function (Component) {
      if(!Component.options.code) Component.options.code = {};
      Component.options.code['allocate'] = "allocate() { return [\"Single submenu\"] }\nallocate(component) { return component.path } // где path это стек меню для каждого компонента\nallocate(component) { return null } // исключение компонента из меню";
    }