export default function (Component) {
      if(!Component.options.code) Component.options.code = {};
      Component.options.code['install'] = "import ContextMenuPlugin from 'rete-context-menu-plugin';\n\neditor.use(ContextMenuPlugin, {\n    searchBar: false,\n    delay: 100,\n    allocate(component) {\n        return ['Submenu'];\n    },\n    rename(component) {\n        return component.name;\n    },\n    items: {\n        'Click me'(){ console.log('Works!') }\n    }\n});";
    }